import nanoid from 'utils/nanoid';

import { NewCard, NewCardProp, SelectionCancel, SelectionDelete, SelectCard, NewMile, SelectMile } from 'types/actions';

// TODO: Actions should not generate IDs
// TODO: Remove isNew and replace with empty string

export const newCard = ({ line, from, until }: NewCardProp): NewCard => ({
  type: 'NEW_CARD',
  payload: {
    id: nanoid(),
    isNew: true,
    line,
    from,
    until,
  },
});

export const selectCard = ({ id }: { id: string }): SelectCard => ({
  type: 'SELECT_CARD',
  payload: {
    id,
    isNew: false,
  },
});

export const newMile = (date: string): NewMile => ({
  type: 'NEW_MILE',
  payload: {
    id: nanoid(),
    date,
    isNew: true,
  },
});

export const selectMile = ({ id }: SelectMile['payload']): SelectMile => ({
  type: 'SELECT_MILE',
  payload: {
    id,
  },
});

export const selectionCancel = (): SelectionCancel => ({
  type: 'SELECTION_CANCEL',
});

export const selectionDelete = ({ id }: { id: string }): SelectionDelete => ({
  type: 'SELECTION_DELETE',
  payload: {
    id,
  },
});
