import { fork, take, TakeEffect, ForkEffect } from 'redux-saga/effects';

import { fetchUserRoadmapsData } from 'state/sagas/roadmaps';
import { AuthSuccess } from 'types/actions';

export function* watchAuth(): Generator<TakeEffect | ForkEffect<void>, void, AuthSuccess> {
  while (true) {
    const action = yield take('AUTH_SUCCESS');
    if (typeof action.payload?.id === 'string') {
      yield fork(fetchUserRoadmapsData, action.payload.id, action.payload.email);
    }
  }
}
