import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'; // TODO: shallowEqual

import { editCard } from 'state/actions/cards';
import { selectionCancel, selectionDelete } from 'state/actions/selection';
import { hasCardChanged, isCardValid } from 'utils/ui';

import { EditScreen as Screen, EditForm } from 'styles/EditScreen';

import EditFields from './EditFields';
import EditTitle from './EditTitle';

import { Card, ExtendedCard, ElementType, State } from 'types/state';
import { CardDispatch } from 'types/actions';
import { editMile } from 'state/actions/miles';

// TODO: Refactor entire file to allow editing of any type of text, pass extra fiels as optional
const DefaultState = {
  id: '',
  text: '',
  line: 0,
  from: '',
  until: '',
} as Card;

const EditScreen = (): JSX.Element | null => {
  const dispatch = useDispatch();
  const selection = useSelector((state: State) => state.selection);
  const selectedElement = useSelector((state: State) => {
    const namespace = selection.type === ElementType.card ? state.cards : state.miles;
    return selection.isNew ? undefined : namespace[selection.id];
  });
  const [isExtraFieldsEnforced, enforceExtraFields] = useState(selectedElement !== undefined);
  const [newSelection, setNewSelection] = useState(DefaultState);
  const [isInvalid, setInvalid] = useState(false);
  useEffect(() => {
    if (isInvalid) {
      setTimeout(() => setInvalid(false), 500);
    }
  }, [isInvalid]);
  useEffect(() => {
    if (selection.id === '') {
      enforceExtraFields(selectedElement !== undefined);
    } else {
      const selected = (selectedElement || selection) as Card | ExtendedCard;
      setNewSelection({
        id: selected.id,
        text: selected.text,
        line: selected.line,
        from: selected.from,
        until: selected.until,
        progress: selected.progress,
      });
    }
    return () => {
      setNewSelection(DefaultState);
    };
  }, [selection, selectedElement]);

  if (selection.type === '' || newSelection.id === '') {
    return null;
  }

  const cardDispatch: CardDispatch = {
    save: () => {
      if (selection.type === ElementType.card && isCardValid(newSelection)) {
        // @ts-ignore
        return dispatch(hasCardChanged(newSelection, selectedElement) ? editCard(newSelection) : selectionCancel());
      } else if (selection.type === ElementType.mile) {
        // There is no check for empty value, because empty milestone value removes the milestone
        // @ts-ignore
        const mileDate = (selection.date || selectedElement.date) as string | undefined;
        return dispatch(editMile({ id: selection.id, text: newSelection.text, date: mileDate }));
      } else {
        // TODO: Show user incorrect fields
        setInvalid(true);
        return;
      }
    },
    cancel: () => dispatch(selectionCancel()),
    delete: () => {
      dispatch(
        selectedElement
          ? selectionDelete({
              id: newSelection.id,
            })
          : selectionCancel()
      );
    },
  };

  const handleClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      cardDispatch.cancel();
    }
  };

  const hasExtraFields = selection.type === ElementType.card && (isExtraFieldsEnforced || Boolean(selectedElement));
  return (
    <Screen onClick={handleClick} data-form="edit">
      <EditForm className={isInvalid ? 'invalid' : undefined}>
        <EditTitle
          type={selection.type}
          selection={newSelection}
          setSelection={setNewSelection}
          cardDispatch={cardDispatch}
          enforceExtraFields={hasExtraFields ? undefined : enforceExtraFields}
        />
        {hasExtraFields ? (
          <EditFields
            selection={newSelection}
            setSelection={setNewSelection}
            cardDispatch={cardDispatch}
            setFocus={isExtraFieldsEnforced}
          />
        ) : null}
      </EditForm>
    </Screen>
  );
};

export default React.memo(EditScreen);
