import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['miles'];

export default (state = initialState.miles, action: Actions): StatePart => {
  switch (action.type) {
    case 'EDIT_MILE':
      return produce(state, (nextState: StatePart) => {
        nextState[action.payload.id] = {
          ...state[action.payload.id],
          ...action.payload,
        };
        return nextState;
      });
    case 'ROADMAP_DATA_FETCH':
      return {
        ...action.payload.miles,
      };
    case 'REMOVE_CURRENT_ROADMAP':
    case 'AUTH_OUT':
      return initialState.miles;
    default:
      return state;
  }
};
