import React, { useState, useMemo } from 'react';
import { useDispatch } from 'react-redux';

import { getDateForTooltip, getMonthName, getMonthString, getTwoDigitDate } from 'utils/dates';
import Day from 'components/Deck/Day';
import Mile from 'components/Deck/Mile';

import { DateType } from 'types';
import C from 'constants/index';
import { MilePlaceholder } from 'styles/MileBlock';
import { newMile } from 'state/actions/selection';

const Month = ({ date, today }: { date: DateType; today: DateType }): JSX.Element => {
  const dispatch = useDispatch();
  const monthName = `${getMonthName(date)} ${date.year}`;
  const days = Array(date.day)
    .fill(date)
    .map(({ year, month }: { year: number; month: number }, i: number) => {
      const day = i + 1;
      const isToday = today.year === year && today.month === month && today.day === day;
      const [weekDay, tip] = getDateForTooltip({ year, month, day });
      return <Day key={day} day={day} weekDay={weekDay} tip={tip} isToday={isToday} />;
    });

  const [mileOffset, setMileOffset] = useState(-1);
  const placeholderStyle = useMemo(
    () => ({
      left: mileOffset * C.DAY.WIDTH + 'px',
      display: mileOffset >= 0 ? 'block' : 'none',
    }),
    [mileOffset]
  );

  const handleMove = (e: React.MouseEvent) => {
    const target = e.target as HTMLElement;
    if (target.tagName.toLowerCase() === 'aside') {
      setMileOffset(Math.floor((e.clientX - target.getBoundingClientRect().left) / C.DAY.WIDTH));
    }
  };
  const handleLeave = () => setMileOffset(-1);
  const handleClick = () => dispatch(newMile(`${getMonthString(date)}-${getTwoDigitDate(mileOffset + 1)}`));

  return (
    <React.Fragment>
      <aside className="monthname">{monthName}</aside>
      <aside className="days">{days}</aside>
      <aside className="mile" onMouseMove={handleMove} onMouseLeave={handleLeave}>
        <Mile date={date} />
        <MilePlaceholder onClick={handleClick} style={placeholderStyle} />
      </aside>
    </React.Fragment>
  );
};

export default React.memo(Month);
