import { EditCard, MoveCard } from 'types/actions';

export const editCard = (props: EditCard['payload']): EditCard => ({
  type: 'EDIT_CARD',
  payload: {
    ...props,
  },
});

export const moveCard = (props: MoveCard['payload']): MoveCard => ({
  type: 'MOVE_CARD',
  payload: {
    ...props,
  },
});
