import { put, call, select, all, fork, Effect, CallEffect, AllEffect } from 'redux-saga/effects';

import {
  addAccountRoadmapByID,
  fetchAccountRoadmaps,
  fetchRoadmapMeta,
  fetchRoadmapShare,
  fetchRoadmapOwner,
  fetchSharedRoadmaps,
  removeSharedByEmailRoadmap,
  removeAccountRoadmap,
} from 'data/data';
import { createAccount } from 'data/newUser';
import { setAccountRoadmaps } from 'state/actions/user';

import { Meta, State } from 'types/state';
import { RoadmapUserSetting } from 'constants/defaults';

type Effects = CallEffect<Meta | undefined> | CallEffect<Meta['share'] | undefined> | CallEffect<string | undefined>;

function* fetchMeta(roadmapID: string): Generator<AllEffect<Effects>> {
  // TODO: Subscribe to meta changes (like we do for roadmaps)
  const [meta, share, owner] = (yield all([
    call(fetchRoadmapMeta, roadmapID),
    call(fetchRoadmapShare, roadmapID),
    call(fetchRoadmapOwner, roadmapID),
  ])) as [Meta, Meta['share'], string];
  return meta
    ? {
        ...meta,
        ...(share && { share }),
        ...(owner && { owner }),
      }
    : undefined;
}

const createEffectMap = (source: string[]): { [key: string]: Effect } =>
  source.reduce((acc, roadmapID) => {
    acc[roadmapID] = call(fetchMeta, roadmapID);
    return acc;
  }, {} as { [key: string]: Effect });

export function* fetchUserRoadmapsData(userID: string, email: string) {
  const ownRoadmaps = ((yield call(fetchAccountRoadmaps, userID)) as State['user']['roadmaps']) || {};
  const ownRoadmapIDs = Object.keys(ownRoadmaps);
  if (!ownRoadmapIDs) {
    const user = (yield select((state: State) => state.user)) as State['user'];
    yield call(createAccount, user);
  }

  const hashedEmail = email.replaceAll('.', ' ');
  const sharedRoadmaps = ((yield call(fetchSharedRoadmaps, hashedEmail)) as Meta['share']) || {};

  const sharedRoadmapIDs = Object.keys(sharedRoadmaps);
  const roadmapIDs = sharedRoadmapIDs.concat(ownRoadmapIDs);

  const mergedRoadmaps = Object.assign(ownRoadmaps, sharedRoadmaps) as State['user']['roadmaps'];
  let metas = {} as State['metas'];
  // Check if the shared roadmaps are accesible; if yes merge with ownRoadmaps before calling 'setAccountRoadmaps'
  if (roadmapIDs.length) {
    metas = yield all(createEffectMap(roadmapIDs));
    for (const roadmapID in metas) {
      if (metas[roadmapID] === undefined) {
        // If fetching meta data failed (access problem) remove roadmaps from the shared list
        delete mergedRoadmaps[roadmapID];
        delete metas[roadmapID];
        // Remove roadmap with no access (doesn't exist or access is limited) from user's access list
        yield fork(removeAccountRoadmap, userID, roadmapID);
        // Remove shared roadmaps from the shared list & when list is empty remove the email
        yield fork(removeSharedByEmailRoadmap, hashedEmail, roadmapID);
      }
    }
    for (const roadmapID in mergedRoadmaps) {
      if (typeof mergedRoadmaps[roadmapID] === 'string') {
        mergedRoadmaps[roadmapID] = RoadmapUserSetting;
        // Adding new roadmaps in user profile
        yield fork(addAccountRoadmapByID, userID, roadmapID);
        // Remove shared roadmaps from the shared list & when list is empty remove the email
        yield fork(removeSharedByEmailRoadmap, hashedEmail, roadmapID);
      }
    }
  }
  // Check the number of account roadmaps (don't add shared roadmaps if user reached available payment plan quota)
  yield put(setAccountRoadmaps(mergedRoadmaps, metas));
}
