export type Card = {
  id: string;
  line: number;
  text: string;
  from: string;
  until: string;
  progress?: number;
};

export type Lines = {
  names: {
    [key: string]: string;
  };
};

export type Long = string;

export type Mile = {
  id: string;
  text: string;
  date: string;
  from?: never;
  until?: never;
  progress?: never;
};

export type Meta = {
  createdAt: number;
  name: string;
  owner: string;
  share?: {
    [email: string]: 'edit' | 'view';
  };
  stats: {
    cards: number;
    from: string;
    until: string;
  };
};

export enum ElementType {
  card = 'card',
  line = 'line',
  mile = 'mile',
  none = '',
}

export type List<Type> = {
  [key: string]: Type;
};

export type Cards = List<Card>;
export type Longs = List<Long>;
export type Miles = List<Mile>;

export interface PersistentState {
  cards: Cards;
  lines: Lines;
  longs: Longs;
  miles: Miles;
}

export interface FetchedData extends PersistentState {
  roadmapID: string;
}

export type SelectedElement = Card | Mile;

export interface ExtendedCard extends Card {
  isNew: boolean;
  type: ElementType;
}
export interface ExtendedMile extends Mile {
  isNew: boolean;
  type: ElementType;
}

export type Selection = ExtendedCard | ExtendedMile;

export interface UserSettings {
  lineNameWidth: number;
}

export interface State extends PersistentState {
  selection: Selection;
  user: {
    id: string;
    name: string;
    email: string;
    photoURL: string;
    fetchig: boolean;
    roadmaps: {
      [key: string]: UserSettings;
    };
    roadmapsSet: boolean;
  };
  metas: List<Meta>;
  ui: {
    currentRoadmapID: string;
    todayTimestamp: number;
    cardsPosition: string;
    fromOffset: string;
    untilOffset: string;
  };
}
