import React, { useRef, useLayoutEffect } from 'react';

import { autoscaleTextarea } from 'utils/ui';

import { EditEntry } from 'styles/EditScreen';

import { SelectedElement, ElementType, Card } from 'types/state';
import { CardDispatch } from 'types/actions';

const EditTitle = ({
  type,
  selection,
  setSelection,
  cardDispatch,
  enforceExtraFields,
}: {
  type: ElementType;
  selection: SelectedElement;
  setSelection: React.Dispatch<React.SetStateAction<Card>>;
  cardDispatch: CardDispatch;
  enforceExtraFields: React.Dispatch<React.SetStateAction<boolean>> | undefined;
}): JSX.Element | null => {
  const inputRef = useRef<HTMLTextAreaElement>(null);
  useLayoutEffect(() => {
    if (inputRef !== null && inputRef.current !== null) {
      autoscaleTextarea(inputRef.current, true);
    }
  }, [selection.id]);

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    autoscaleTextarea(e.target as HTMLTextAreaElement);
    setSelection((prevSelection) => ({
      ...prevSelection,
      text: e.target.value,
    }));
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key.toLocaleLowerCase() === 'enter') {
      e.preventDefault();
      cardDispatch.save();
    } else if (e.key.toLocaleLowerCase() === 'tab') {
      e.preventDefault();
      if (type === ElementType.card && enforceExtraFields) {
        enforceExtraFields(true);
      }
    }
  };

  return (
    <EditEntry
      ref={inputRef}
      autoFocus={true}
      onChange={handleChange}
      onKeyDown={handleKeyDown}
      placeholder={type === ElementType.mile ? 'Milestone name' : 'Task name'}
      value={selection.text}
    />
  );
};

export default React.memo(EditTitle);
