import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { selectMile } from 'state/actions/selection';

import { MileBlock } from 'styles/MileBlock';

import { DateType } from 'types';
import { State } from 'types/state';
import { getMonthString } from 'utils/dates';
import C from 'constants/index';

const Mile = ({ date }: { date: DateType }): JSX.Element => {
  const milesObject = useSelector((state: State) => state.miles);
  const dispatch = useDispatch();

  const combinedDate = getMonthString(date);
  const MonthMiles = Object.values(milesObject)
    .filter((mile) => mile.date.slice(0, 7) === combinedDate)
    .map((mile) => {
      const dayOffset = (parseInt(mile.date.slice(8)) - 1) * C.DAY.WIDTH;
      const todayMileStyle = { left: dayOffset + 'px' };
      const handleClick = () => dispatch(selectMile({ id: mile.id }));

      return <MileBlock key={mile.id} onClick={handleClick} data-text={mile.text} style={todayMileStyle} />;
    });
  return <>{MonthMiles}</>;
};

export default React.memo(Mile);
