import firebase from 'firebase/app';
import { eventChannel, EventChannel, END } from 'redux-saga';

import { EditLineName } from 'types/actions';
import { Card, Meta, Mile, PersistentState, State } from 'types/state';
import { RoadmapUserSetting } from 'constants/defaults';

const testConnection = async (ref: firebase.database.Reference, emergencyBreak: () => void) => {
  try {
    const snapshot = await ref.once('value');
    await snapshot.val();
  } catch (error) {
    // TODO: Log the error
    // console.error(error);
    emergencyBreak();
  }
};

export const getRoadmapContent = (roadmapID: string): EventChannel<PersistentState> =>
  eventChannel((emiter) => {
    const listener = firebase.database().ref(`roadmaps/${roadmapID}/content`);
    const emergencyBreak = () => void emiter(END) || listener.off();
    testConnection(listener, emergencyBreak);
    listener.on('value', (snapshot) => {
      // TODO: Log error in case permission is demied
      const value = snapshot.val() as PersistentState;
      emiter(
        value ?? {
          roadmapID: roadmapID,
        }
      );
    });
    return emergencyBreak;
  });

const fetchRemoteContent = async <T>(path: string): Promise<T | undefined> => {
  let content;
  try {
    const contentSnapshot = await firebase.database().ref(path).once('value');
    content = contentSnapshot.val();
  } catch (error) {
    // TODO: Log this error in the storage
    // TODO: Show user a warning
    // console.error(error);
  } finally {
    return content;
  }
};

export const fetchAccountRoadmaps = async (userID: string) =>
  await fetchRemoteContent<State['user']['roadmaps']>(`users/${userID}/roadmaps`);

export const removeAccountRoadmap = async (userID: string, roadmapID: string): Promise<void> =>
  await firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}`).remove();

export const fetchSharedRoadmaps = async (hashedEmail: string) =>
  await fetchRemoteContent<Meta['share']>(`share/${hashedEmail}`);

export const addAccountRoadmapByID = async (userID: string, roadmapID: string): Promise<firebase.database.Reference> =>
  await firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}`).update(RoadmapUserSetting);

export const removeSharedByEmailRoadmap = async (hashedEmail: string, roadmapID: string): Promise<void> =>
  await firebase.database().ref(`share/${hashedEmail}/${roadmapID}`).remove();

export const fetchRoadmapMeta = async (roadmapID: string) =>
  await fetchRemoteContent<Meta>(`roadmaps/${roadmapID}/meta/`);

export const fetchRoadmapShare = async (roadmapID: string) =>
  await fetchRemoteContent<Meta['share']>(`roadmaps/${roadmapID}/share/`);

export const fetchRoadmapOwner = async (roadmapID: string) =>
  await fetchRemoteContent<Meta['owner']>(`roadmaps/${roadmapID}/owner/`);

export const updateRemoteMeta = async (roadmapID: string, stats: Meta['stats']): Promise<firebase.database.Reference> =>
  await firebase
    .database()
    .ref(`roadmaps/${roadmapID}/meta/stats/`)
    .update({
      ...stats,
    });

export const updateRemoteCardData = (currentRoadmapID: string, card: Card | { id: string }): void => {
  const { id, ...content } = card;
  const ref = firebase.database().ref(`roadmaps/${currentRoadmapID}/content/cards/${id}`);
  if (Object.keys(content).length) {
    void ref.update({
      progress: null,
      ...content,
    });
  } else {
    void ref.remove();
  }
};

export const updateRemoteMileData = (currentRoadmapID: string, mile: Mile): void => {
  const { id, ...content } = mile;
  const ref = firebase.database().ref(`roadmaps/${currentRoadmapID}/content/miles/${id}`);
  if (Object.keys(content).length && content.text.length) {
    void ref.update({
      ...content,
    });
  } else {
    void ref.remove();
  }
};

export const updateRemoteLineData = (currentRoadmapID: string, { line, text }: EditLineName['payload']): void => {
  const ref = firebase.database().ref(`roadmaps/${currentRoadmapID}/content/lines/names/`);
  void ref.update({ [line]: text.length ? text : null });
};

export const updateRemoteLong = (roadmapID: string, cardID: string, long: string): void => {
  const ref = firebase.database().ref(`roadmaps/${roadmapID}/content/longs/${cardID}/`);
  if (long) {
    void ref.set(long);
  } else {
    void ref.remove();
  }
};

export const addRemoteAccountRoadmap = async (userID: string, time: number, roadmapName: string): Promise<string> => {
  const roadmapID = firebase.database().ref(`users/${userID}/roadmaps/`).push().key;
  if (roadmapID) {
    await firebase
      .database()
      .ref(`users/${userID}/roadmaps/`)
      .update({
        [roadmapID]: RoadmapUserSetting,
      });
    await firebase.database().ref(`roadmaps/${roadmapID}/owner/`).set(userID);
    await firebase
      .database()
      .ref(`roadmaps/${roadmapID}/meta/`)
      .update({
        name: roadmapName,
        createdAt: time,
        stats: {
          from: '',
          cards: 0,
          until: '',
        },
      });
    return roadmapID;
  }
  return '';
};

type SettingProp = {
  userID: string;
  roadmapID: string;
  settingName: string;
  settingValue: string | number | undefined;
};

export const setAccountRoadmapSetting = ({ userID, roadmapID, settingName, settingValue }: SettingProp): void => {
  const ref = firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}/${settingName}`);
  if (settingValue !== undefined) {
    void ref.set(settingValue);
  } else {
    void ref.remove();
  }
};

export const removeRemoteAccountRoadmap = (userID: string, roadmapID: string): void => {
  void firebase.database().ref(`users/${userID}/roadmaps/${roadmapID}/`).remove();
};
