import { fork, take, select, TakeEffect, SelectEffect, ForkEffect } from 'redux-saga/effects';

import { updateRemoteMileData } from 'data/data';
// import { updateMeta } from 'state/sagas/meta';

import { MileEditActions } from 'types/actions';
import { Miles, State } from 'types/state';

type ChangeActions = MileEditActions | Miles | string | undefined;

export function* watchMileChange(): Generator<TakeEffect | SelectEffect | ForkEffect<unknown>, void, ChangeActions> {
  while (true) {
    const action = (yield take(['EDIT_MILE'])) as MileEditActions;
    const miles = (yield select((state: State) => state.miles)) as Miles;
    const mile = miles[action.payload.id];
    const currentRoadmapID = (yield select((state: State) => state.ui.currentRoadmapID)) as string;
    if (currentRoadmapID) {
      yield fork(updateRemoteMileData, currentRoadmapID, mile || { id: action.payload.id });
      // yield fork(updateMeta, currentRoadmapID, cards);
    }
  }
}
