import React from 'react';
import Private from '@material-ui/icons/VerifiedUser';
import Shared from '@material-ui/icons/PeopleAlt';
import Edit from '@material-ui/icons/Edit';
import View from '@material-ui/icons/RemoveRedEye';

import OverviewBadge from 'components/Overview/OverviewBadge';

import { Meta } from 'types/state';

const AccessState = ({ meta }: { meta: Meta }): JSX.Element => {
  const option = meta.owner ? (meta.share ? 'shared' : 'private') : meta.share ? 'edit' : 'view';
  const sharedNumber = meta.share ? Object.keys(meta.share).length : 0;
  const access = {
    private: { title: `It's your private plan, nobody else has access to it`, node: <Private /> },
    shared: { title: `Your own this plan. It's shared with ${sharedNumber} account(s)`, node: <Shared /> },
    edit: { title: `Owner shared this plan with ${sharedNumber} account(s). You have edit rights`, node: <Edit /> },
    view: { title: 'Owner gave you view access to this plan', node: <View /> },
  };
  return <OverviewBadge title={access[option].title} caption="Access" children={access[option].node} />;
};

export default React.memo(AccessState);
